import React from "react"
import { FaTwitter, FaInstagram,FaLinkedin, FaGithub} from 'react-icons/fa';

class Footer extends React.Component {
    constructor(props) {
        super(props)
    }

    render() {
        return (
            <footer>
               
                 <a href=""><FaTwitter className="socialMedia"/></a>
                 <a href=""><FaInstagram className="socialMedia"/></a>
                 <a href=""><FaLinkedin className="socialMedia"/></a>
                 <a href=""><FaGithub className="socialMedia"/></a>
                
            </footer>
        )
    }
}

export default Footer