import React, { Component } from "react";
import "../styles/contactarme.css";
import axios from "axios";

class Contactarme extends Component {
  email = React.createRef();
  asunto = React.createRef();
  mensaje = React.createRef();

  comprobarCambios = () => {
    var email = this.email.current.value;
    var asunto = this.asunto.current.value;
    var mensaje = this.mensaje.current.value;
    this.setState({
      email: email,
      asunto: asunto,
      mensaje: mensaje
    });
  };

  constructor() {
    super();
    const state = {
      email: "",
      asunto: "",
      mensaje: ""
    };
    this.enviarEmail = this.enviarEmail.bind(this);
  }

  async enviarEmail(e) {
    e.preventDefault();
    const { email, asunto, mensaje } = this.state;
    console.log("cuerpo mensaje ", { email, asunto, mensaje } )
    try {
      const form = await axios.post("http://localhost:3001/api/form", { //https://backendloleiporahi.herokuapp.com/api/form
        email,
        asunto,
        mensaje
      });
    } catch(error) {
      console.log(error);
      console.log("murio");
    }
  }

  render() {
    return (
      <div>
        <form className="formulario" onSubmit={this.enviarEmail}>
          <div className="field-wrap">
            <input
              type="email"
              name="email"
              placeholder="Email"
              onChange={this.comprobarCambios}
              className="form-control"
              ref={this.email}
            />
          </div>
          <div className="field-wrap">
            <input
              type="text"
              placeholder="Nombre"
              name="asunto"
              onChange={this.comprobarCambios}
              className="form-control"
              ref={this.asunto}
            />
          </div>
          <div className="field-wrap">
            <textarea
              name="mensaje"
              placeholder="Escribi tu mensaje"
              onChange={this.comprobarCambios}
              className="form-control"
              ref={this.mensaje}
            ></textarea>
          </div>
          <div>
            <button type="submit" className="button" >
              Enviar mensaje
            </button>
          </div>
        </form>
      </div>
    );
  }
}

export default Contactarme;